<template>
    <div>
        <section id="post-project">
            <div class="container">
                <div class="post-project">
                    <div class="row">
                        <div class="col-sm-12 col-md-12 col-lg-6 offset-3">
                            <h1 class="auth-title">Login to Your Account</h1>
                            <form method="post" class="submit-info">
                                <response-message :data="errorData"></response-message>
                                <div class="need-info">
                                    <p class="info-title">Email Address</p>
                                    <input v-model="formModelLogin.email" autocomplete="email" id="email" name="email" type="text" class="form-control" placeholder="Email Address">
                                </div>
                                <div class="need-info">
                                    <p class="info-title">Password</p>
                                    <p v-if="passwordVisibility === false" @click=" () => { passwordVisibility = true } " class="tooltip-icon"><a href="javascript:;"><i class="fas fa-eye"></i></a></p>
                                    <p v-else @click=" () => { passwordVisibility = false } " class="tooltip-icon"><a href="javascript:;"><i class="fas fa-eye-slash"></i></a></p>
                                    <input v-model="formModelLogin.password" autocomplete="password" id="password" name="password" :type=" passwordVisibility === false ? 'password' : 'text' " class="form-control" placeholder="Password">
                                </div>
                                <div class="remember">
                                    <label class="checkBoxCont pull-left">
                                        Remember Me
                                        <input type="checkbox" value="0"
                                               v-model="formModelLogin.remember" />
                                        <span class="checkmark"></span>
                                    </label>
                                    <a
                                            data-toggle="tab" href="#forgot" role="tab"
                                            class="forgot pull-right"
                                    >Forgot my Password</a>
                                </div>
                                <div class="post-job-btn">
                                    <button @click.prevent="login" id="login-btn" type="submit" class="button-ymp">Login</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "login",
        data() {
            return {
                formModelLogin: {
                    remember: false,
                    email: "",
                    password: "",
                },
                passwordVisibility: false,
                errorData: {},
            }
        },
        mounted() {
            $("title").html( 'Login | ' + this.$store.state.appName );
            console.log(this.$ls.getStorage("algebra_last_url"))
        },
        methods: {
            resetForm() {
                this.formModelLogin.email = "";
                this.formModelLogin.password = "";
            },
            login() {
                if (this.formModelLogin.email === "" ) {
                    this.errorData = {
                        message: "Sorry an email is required",
                        isError: true,
                    };
                }else if(this.formModelLogin.password === "" ) {
                    this.errorData = {
                        message: "Sorry a password is required",
                        isError: true,
                    };
                }else {
                    $("#login-btn").html('Processing....');
                    $("#login-btn").attr("disabled",true);

                    const requestPayload = {
                        email: this.formModelLogin.email,
                        password: this.formModelLogin.password,
                    };

                    this.axios.post('user/login', requestPayload)
                        .then(response => {
                            console.log(response.data.data)
                            var storeTicket = {
                                token: response.data.data.token,
                                token_generation: response.data.data.expires_at * 1000,
                            };
                            var storeFansData = {
                                id: response.data.data.id,
                                username: response.data.data.username,
                                email: response.data.data.email,
                                user_type: response.data.data.user_type,
                            };

                            if (this.$ls.clearStorage("algebraToken") && this.$ls.clearStorage("algebraData")) {
                                this.$ls.setStorage("algebraToken", storeTicket);
                                this.$ls.setStorage("algebraData", storeFansData);
                            }

                            this.resetForm();

                            // show the success message
                            this.errorData = {
                                message: "Logged in Redirecting ....",
                                isError: "success",
                            };
                            // reload the browser
                            window.eventBus.$emit("loggedIn",true);
                            if (this.$ls.checkStorage("algebra_from_url")) {
                                let currentPath = this.$ls.getStorage("algebra_from_url")[0].fullPath;
                                currentPath = window.location.pathname;
                                window.location = currentPath;
                            }else {
                                let currentPath = window.location.pathname;
                                window.location = currentPath;
                            }

                        })
                        .catch(error => {
                            console.log('error', error.response)
                            $("#login-btn").html('Login');
                            $("#login-btn").attr("disabled",false);
                            this.errorData = {
                                message: error.response.status === 401
                                    ? "Please check your Login Details"
                                    : error.response.status === 400
                                    ? "Sorry an error from our end"
                                    : "Sorry an error occurred, check your internet",
                                isError: true,
                            };
                        })
                }
            },
        },
    }
</script>

<style scoped>
.submit-info {
    width: 100%;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 8px 12px rgba(0,0,0,.1);
    min-height: 500px;
}
    .auth-title {
        text-align: center;
        margin: 20px 0;
        font-size: 26px;
        color: #6c757d;
        text-shadow: 0 7px 6px rgba(0,0,0,.1);
    }
</style>
